.login-form {
    position: center;
    max-width: 30%;
    height: fit-content;
    border: 2px solid rgba(0, 0, 0, 0.596);
    background-color:rgba(207, 198, 198, 0.719);
    margin-top: 10%;
    margin-left: 35%;
    border-radius: 20px;
}
.login-form h2{
 text-align: center;
   background-color: rgb(64, 105, 182);
   margin: 0px;
   padding-top: 10px;
   padding-bottom: 10px;
   border-top-left-radius: 15px;
   border-top-right-radius: 15px;
   font-size: 30px;
   color: whitesmoke;
}
.login-form form label{
   font-size:medium;
}
.form{
  text-align: left;
  padding-left: 25px;
  padding-top: 25px;
}
.form p{
    display: block;
}
button{
    margin-top: 10px;
    padding: 5px 10px;
    border-radius: 5px;
    border: 1px solid green;
}
.login-form input{
    width: 90%;
    height: 27px;
}
